<template>
  <div class="monthly-schedule" id="avoid-split">
    <h1>{{ formattedDate }}工作表</h1>
    <DatePicker v-model:value="selectedDate" type="month" :clearable="false" />
    <button @click="generateMonthlySchedule" class="generate-button" :disabled="!isDataReady">生成月班表</button>
    <button @click="submitSchedule" class="submit-button" :disabled="!monthlySchedule.length">儲存班表</button>
    <button @click="generatePDF" class="generate-pdf-button">生成 PDF</button>
    <div v-if="error" class="error-message">{{ error }}</div>
    <div v-if="isLoading" class="loading-message">加載中...</div>
    <table v-if="monthlySchedule.length">
      <thead>
        <tr>
          <th>姓名</th>
          <th v-for="day in daysInMonth" :key="day">
            {{ day }}
            <br>
            {{ getDayName(new Date(selectedDate.getFullYear(), selectedDate.getMonth(), day).getDay()) }}
          </th>
          <th>D</th>
          <th>V</th>
          <th>總時數</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(nurse, nurseIndex) in monthlySchedule" :key="nurse.name">
          <td>{{ nurse.name }}</td>
          <td v-for="(shift, shiftIndex) in nurse.shifts" :key="shiftIndex" @click="toggleShift(nurseIndex, shiftIndex)"
            :class="getShiftClass(shift)">
            {{ shift }}
          </td>
          <td>{{ countShifts(nurse.shifts, 'D') }}</td>
          <td>
            <input type="number" v-model.number="nurse.vacationDays" @input="updateNurseData(nurseIndex)" min="0"
              class="editable-input">
          </td>
          <td>{{ calculateTotalHours(nurse.shifts) }}</td>
        </tr>
      </tbody>
      <tfoot>
        <tr v-for="shiftType in ['A', 'E', 'N', 'D',]" :key="shiftType">
          <td>{{ shiftType }}</td>
          <td v-for="day in daysInMonth" :key="day">{{ countDailyShifts(day - 1, shiftType) }}</td>
          <td colspan="4"></td>
        </tr>
      </tfoot>
    </table>
  </div>
</template>

<script>
import { ref, computed, watch, onMounted } from 'vue'
import { useStore } from 'vuex'
import DatePicker from 'vue-datepicker-next'
import 'vue-datepicker-next/index.css'
import html2pdf from 'html2pdf.js'
import axios from 'axios'

export default {
  name: 'MonthlySchedule',
  components: { DatePicker },
  setup() {
    const store = useStore()
    const selectedDate = ref(new Date())
    const monthlySchedule = ref([])
    const error = ref('')
    const isLoading = ref(false)
    const isDataReady = ref(false)

    const dayNames = ['日', '一', '二', '三', '四', '五', '六']
    const daysInMonth = computed(() => new Date(selectedDate.value.getFullYear(), selectedDate.value.getMonth() + 1, 0).getDate())

    const formattedDate = computed(() => {
      const year = selectedDate.value.getFullYear()
      const month = selectedDate.value.getMonth() + 1
      return `${year}年${month}月`
    })


    const generatePDF = () => {
      const year = selectedDate.value.getFullYear()
      const month = (selectedDate.value.getMonth() + 1).toString().padStart(2, '0')
      const fileName = `恩主公麻醉科護理人員${year}${month}月班表.pdf`

      // 暫時隱藏按鈕、輸入框和 DatePicker
      const buttons = document.querySelectorAll('.generate-button, .submit-button, .generate-pdf-button, .editable-input')
      const datePicker = document.querySelector('.mx-datepicker')
      buttons.forEach(button => button.style.display = 'none')

      if (datePicker) {
        datePicker.style.display = 'none'; // 隱藏 DatePicker
      }

      // 動態設置 PDF 標題
      const originalTitle = document.querySelector('h1').innerText
      document.querySelector('h1').innerText = `恩主公麻醉科護理人員${year}${month}月班表`

      const element = document.querySelector('.monthly-schedule')

      const opt = {
        margin: [0, 0], // 無邊距
        filename: fileName,
        image: { type: 'jpeg', quality: 1.0 },  // 確保圖像質量最高
        html2canvas: { scale: 4, useCORS: true, backgroundColor: null },  // 提高畫布分辨率並確保背景透明
        jsPDF: { unit: 'mm', format: 'a3', orientation: 'portrait' },  // 使用大尺寸紙張格式防止分頁
        pagebreak: { mode: 'avoid-all' }  // 避免分頁
      }

      html2pdf().set(opt).from(element).save().then(() => {
        // 恢復按鈕、輸入框和 DatePicker 的顯示
        buttons.forEach(button => button.style.display = '')
        if (datePicker) {
          datePicker.style.display = ''; // 恢復 DatePicker
        }
        document.querySelector('h1').innerText = originalTitle  // 恢復原始標題
      })
    }

    const saveToDatabase = async () => {
      const scheduleData = {
        date: selectedDate.value.toISOString(),
        monthlySchedule: monthlySchedule.value,
        timestamp: new Date().toISOString()
      }

      try {
        const response = await axios.post('/api/schedule', scheduleData)
        if (response.status === 200) {
          console.log('班表成功保存到數據庫')
        } else {
          throw new Error('保存失敗')
        }
      } catch (error) {
        console.error('保存班表時發生錯誤:', error)
        error.value = '保存班表失敗，請稍後再試'
      }
    }

    const generateMonthlySchedule = async () => {
      if (!isDataReady.value) {
        error.value = '數據尚未準備就緒，請稍後再試。'
        return
      }

      isLoading.value = true
      error.value = ''

      try {
        const formulaSchedules = store.getters['schedule/getFormulaSchedules']
        const porFormulaSchedules = store.getters['schedule/getPORFormulaSchedules']
        const leaderFormulaSchedules = store.getters['schedule/getLeaderFormulaSchedules']
        const secretaryFormulaSchedules = store.getters['schedule/getSecretaryFormulaSchedules']
        const allNurses = store.getters['staff/allNurses']

        if (!formulaSchedules || !porFormulaSchedules || !leaderFormulaSchedules || !secretaryFormulaSchedules || !allNurses) {
          throw new Error('缺少公式班表或護士數據')
        }

        const year = selectedDate.value.getFullYear()
        const month = selectedDate.value.getMonth()
        const daysInMonth = new Date(year, month + 1, 0).getDate()
        const firstDayOfMonth = new Date(year, month, 1).getDay()

        const formulaStartDay = (firstDayOfMonth + 6) % 7

        // 覆蓋月班表
        monthlySchedule.value = allNurses.map(nurse => {
          const shifts = []

          // 確定護理人員應該使用的公式班序列
          let nurseFormulas = formulaSchedules
          if (nurse.role === 'POR') {
            nurseFormulas = porFormulaSchedules
          } else if (nurse.role === 'leader') {
            nurseFormulas = leaderFormulaSchedules
          } else if (nurse.role === 'secretary') {
            nurseFormulas = secretaryFormulaSchedules
          }

          let formulaIndex = 0

          if (nurse.group > 0 && nurse.group <= nurseFormulas.length) {
            formulaIndex = nurse.group - 1
          }

          for (let i = 0; i < daysInMonth; i++) {
            let shift = 'O'

            if (nurse.role === 'boss') {
              // 為阿長應用特殊規則，從週一開始 'A' 班
              const currentDate = new Date(year, month, i + 1)
              const dayOfWeek = currentDate.getDay()
              shift = (dayOfWeek >= 1 && dayOfWeek <= 5) ? 'A' : 'O'
            } else {
              const dayInFormula = (formulaStartDay + i) % 7
              const weekNumber = Math.floor((formulaStartDay + i) / 7)




              // 使用護理人員的公式班表數據，覆蓋該天的班表
              if (nurse.group !== 0) {
                const formula = nurseFormulas[(formulaIndex + weekNumber) % nurseFormulas.length]
                if (formula && formula.shifts) {
                  shift = formula.shifts[dayInFormula] || 'O'  // 如果當天班表為空，使用 'O'
                }
              }
            }
            shifts.push(shift)
          }

          return {
            name: nurse.name,
            role: nurse.role,
            group: nurse.group,
            shifts,
            vacationDays: 0,
            accumulatedLeave: 0
          }
        })

        await saveToDatabase()
      } catch (e) {
        console.error('生成月班表時發生錯誤:', e)
        error.value = e.message || '生成月班表時發生錯誤'
      } finally {
        isLoading.value = false
      }
    }

    const submitSchedule = async () => {
      if (monthlySchedule.value.length === 0) {
        error.value = '沒有可提交的班表數據'
        return
      }
      await saveToDatabase()
    }

    const toggleShift = async (nurseIndex, shiftIndex) => {
      const currentShift = monthlySchedule.value[nurseIndex].shifts[shiftIndex]
      const shiftTypes = ['D', 'A', 'N', 'O', 'K', 'C', 'F', 'E', 'B']
      const nextShiftIndex = (shiftTypes.indexOf(currentShift) + 1) % shiftTypes.length
      monthlySchedule.value[nurseIndex].shifts[shiftIndex] = shiftTypes[nextShiftIndex]
      await saveToDatabase()
    }

    const updateNurseData = async () => {
      await saveToDatabase()
    }

    const getShiftClass = (shift) => {
      return {
        'shift-d': shift === 'D',
        'shift-a': shift === 'A',
        'shift-n': shift === 'N',
        'shift-k': shift === 'K',
        'shift-c': shift === 'C',
        'shift-f': shift === 'F',
        'shift-e': shift === 'E',
        'shift-b': shift === 'B',
        'shift-o': shift === 'O'
      }
    }

    const getDayName = (day) => dayNames[day]

    const countShifts = (shifts, type) => shifts.filter(shift => shift === type).length

    const calculateTotalHours = (shifts) => {
      const hourMapping = { 'D': 10, 'A': 8, 'N': 8, 'O': 0, 'K': 8, 'C': 8, 'F': 8, 'E': 4, 'B': 8 }
      return shifts.reduce((total, shift) => total + (hourMapping[shift] || 0), 0)
    }

    const countDailyShifts = (day, type) => {
      return monthlySchedule.value.filter(nurse => nurse.shifts[day] === type).length
    }

    const loadFromDatabase = async () => {
      try {
        const year = selectedDate.value.getFullYear()
        const month = selectedDate.value.getMonth() + 1
        const response = await axios.get(`/api/schedule/${year}/${month}`)

        if (response.status === 200 && response.data) {
          monthlySchedule.value = response.data.monthlySchedule
          error.value = ''
        } else {
          monthlySchedule.value = []
          error.value = '該月份暫無班表'
        }
      } catch (e) {
        console.error('從數據庫加載班表時發生錯誤:', e)
        error.value = '加載班表失敗，請稍後再試'
        monthlySchedule.value = []
      }
    }

    onMounted(async () => {
      try {
        await store.dispatch('schedule/fetchFormulaSchedules')
        await store.dispatch('schedule/fetchPORFormulaSchedules')
        await store.dispatch('schedule/fetchLeaderFormulaSchedules')
        await store.dispatch('schedule/fetchSecretaryFormulaSchedules')
        await store.dispatch('staff/initializeNurses')
        await loadFromDatabase()
        isDataReady.value = true
      } catch (e) {
        console.error('初始化數據時發生錯誤:', e)
        error.value = '載入數據失敗，請刷新頁面重試'
      }
    })

    watch(selectedDate, () => {
      loadFromDatabase()
    })

    return {
      selectedDate,
      monthlySchedule,
      daysInMonth,
      formattedDate,
      error,
      isLoading,
      isDataReady,
      generateMonthlySchedule,
      submitSchedule,
      generatePDF,  // 將生成 PDF 的方法暴露到模板中
      toggleShift,
      updateNurseData,
      getShiftClass,
      getDayName,
      countShifts,
      calculateTotalHours,
      countDailyShifts,
      saveToDatabase,
      loadFromDatabase,
    }
  }
}
</script>

<style scoped>
.monthly-schedule {
  padding: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  font-size: 12px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 4px;
  text-align: center;
}

th {
  background-color: #f2f2f2;
}

.shift-d {
  background-color: #a08887;
}

.shift-a {
  background-color: #d9d06e;
}

.shift-n {
  background-color: #8387da;
}

.shift-o {
  background-color: #E0FFFF;
}

.shift-k {
  background-color: #8AA6C1;
}

.shift-c {
  background-color: #67dcbd;
}

.shift-f {
  background-color: #FFA07A;
}

.shift-e {
  background-color: #FFB6C1;
}

.shift-b {
  background-color: #FFDAB9;
}

.generate-button,
.submit-button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.generate-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
}

.submit-button {
  background-color: #008CBA;
  color: white;
  border: none;
  border-radius: 4px;
}

.editable-input {
  width: 40px;
  text-align: center;
  border: 1px solid #ddd;
  padding: 2px;
}

.generate-pdf-button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #FF9800;
  /* 橙色背景，適合強調生成 PDF */
  color: white;
  /* 白色文字 */
  border: none;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  /* 平滑的背景顏色過渡 */
}

.generate-pdf-button:hover {
  background-color: #E67E22;
  /* 當鼠標懸停時，變暗的橙色 */
}

@media print {

  body,
  html {
    margin: 0;
    padding: 0;
  }

  .monthly-schedule {
    page-break-inside: avoid;
    /* 避免內容分頁 */
    page-break-before: avoid;
  }
}
</style>