import axios from 'axios';

const API_URL = process.env.VUE_APP_API_URL || 'http://localhost:8000';

export default {
  namespaced: true,
  state: {
    monthlySchedule: null,
    regularFormula: null,
    porFormula: null,
    leaderFormula: null,
    secretaryFormula: null,
    error: null
  },
  mutations: {
    setMonthlySchedule(state, schedule) {
      state.monthlySchedule = schedule;
    },
    setFormula(state, { type, formula }) {
      state[`${type}Formula`] = formula;
    },
    setError(state, error) {
      state.error = error;
    }
  },
  actions: {
    async saveMonthlySchedule({ commit }, schedule) {
      try {
        const response = await axios.post(`${API_URL}/api/schedule`, schedule);
        if (response.status === 200) {
          commit('setMonthlySchedule', response.data);
          commit('setError', null);
        } else {
          throw new Error('Failed to save monthly schedule');
        }
      } catch (error) {
        console.error('Failed to save monthly schedule:', error);
        commit('setError', 'Failed to save monthly schedule. Please try again.');
      }
    },
    async loadMonthlySchedule({ commit }, { year, month }) {
      try {
        const response = await axios.get(`${API_URL}/api/schedule/${year}/${month}`);
        if (response.status === 200) {
          commit('setMonthlySchedule', response.data);
          commit('setError', null);
        } else {
          throw new Error('Failed to load monthly schedule');
        }
      } catch (error) {
        console.error('Failed to load monthly schedule:', error);
        commit('setError', 'Failed to load monthly schedule. Please try again.');
      }
    },
    async saveFormula({ commit }, { type, formula }) {
      try {
        const response = await axios.post(`${API_URL}/api/formula/${type}`, formula);
        if (response.status === 200) {
          commit('setFormula', { type, formula: response.data });
          commit('setError', null);
        } else {
          throw new Error(`Failed to save ${type} formula`);
        }
      } catch (error) {
        console.error(`Failed to save ${type} formula:`, error);
        commit('setError', `Failed to save ${type} formula. Please try again.`);
      }
    },
    async loadFormula({ commit }, type) {
      try {
        const response = await axios.get(`${API_URL}/api/formula/${type}`);
        if (response.status === 200) {
          commit('setFormula', { type, formula: response.data });
          commit('setError', null);
        } else {
          throw new Error(`Failed to load ${type} formula`);
        }
      } catch (error) {
        console.error(`Failed to load ${type} formula:`, error);
        commit('setError', `Failed to load ${type} formula. Please try again.`);
      }
    },
  },
  getters: {
    monthlySchedule: state => state.monthlySchedule,
    regularFormula: state => state.regularFormula,
    porFormula: state => state.porFormula,
    leaderFormula: state => state.leaderFormula,
    secretaryFormula: state => state.secretaryFormula,
    error: state => state.error,
    getFormulaSchedules: (state) => (type) => {
      const formula = state[`${type}Formula`];
      return formula ? formula.formula_data : null;
    }
  }
};